import React from 'react'
import { Helmet } from "react-helmet";
import { useTranslation } from 'react-i18next';

function MetaDecorator({ url, title, description, type}) {

  const { i18n } = useTranslation();
  let lang = i18n.language;

  return (
    <Helmet>
    <meta property="og:title" content= {title} />
    <meta property="og:site_name" content="Dreamcode Software"></meta>
    <meta property="og:url" content={`https://www.dreamcodesoft.com/${url}`} />
    <meta property="og:type" content={type}></meta>
    <meta property="og:description" content= {description} />
    <title>{title}</title>
    <link rel="alternate" hreflang={lang} href={`https://www.dreamcodesoft.com/${lang}`} />
    <meta name="description" content={description} />
    <link rel="canonical" href={`https://www.dreamcodesoft.com/${url}`}/>
</Helmet>
  )
}

export default MetaDecorator;